import { useState } from "react"

export const _useAccordion = () => {
  const [isOpen, setIsOpen] = useState(false)

  const togglePanel = () => setIsOpen(!isOpen)
  const openPanel = () => setIsOpen(true)
  const closePanel = () => setIsOpen(false)

  return {
    isOpen,
    togglePanel,
    openPanel,
    closePanel,
  }
}

